#projectfunc{
    width: 100%;
    height: 100%;
    background-color: #FFDAB9;
    display: flex;
    align-content: space-between;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    overflow-y: auto;
    overflow-x: auto;
    flex: 1 1 auto;

}
#projectfunc::-webkit-scrollbar {
    width: 4px; 
  }
#projectfunc::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px; 
}
  
#projectfunc::-webkit-scrollbar-thumb:hover {
    background-color: lightgrey;
}
  
#projectfunc::-webkit-scrollbar-track {
    background-color: #ddd;
}
#projectcontainer{
    display: block;
    width: 100%;
    height: 100%;
    overflow-x: auto;
    overflow-y: auto;
}
