.icons{
animation: slide 2s;
gap: 15px;
display: flex;
position: absolute;
right: 20px;
}
.icon:hover{
    transform: scale(1.2);
    transition: transform 0.2s;
}
.icon{
    display: block;
    height: 30px;
    color: #444444;
}
.icon:active{
    color: lightcoral
}

@keyframes slide {
    from {
      transform: translateY(-25px);
    }
    to {
      transform: translateY(0px);
    }
  }

  @media (max-width: 767px) {
    .icons{
      animation: slide 2s;
      gap: 5px;
      display: flex;
      flex: 1;
      right: 5px;
      }
  }