#SkillsBrag{
    background-color: white;
    width: 100%;
    height: 100%;
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
}  

#skills{
    /* background-color: #eafff4; */
    background: url("https://images.pexels.com/photos/1323550/pexels-photo-1323550.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
    background-size: cover;
    opacity: 0.8;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
}

h1{
    font-family:'Courier New', Courier, monospace;
    flex: 1;
    text-align: center;
    align-content: center;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}
#skillp{
    flex: 6 1;
    line-height: 1.2;
    overflow-y: auto;
    align-content:flex-start;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    font-size: larger;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    scrollbar-width: thin;
}

  
#skillp::-webkit-scrollbar {
    width: 4px; 
  }
  
#skillp::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px; 
  }
  
  #skillp::-webkit-scrollbar-thumb:hover {
    background-color: lightgrey;
  }
  
  #skillp::-webkit-scrollbar-track {
    background-color: #ddd;
  }


#coreinterestsp{
    text-align: center;
    flex: 1 1;
    line-height: 1.2;
    align-content:flex-start;
    display: flex;
    flex-wrap: wrap;
    font-size: larger;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
}
#certis{
    flex: 5;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
    display: flex;
 
}
#oracle1{
    flex: 4 1;
    align-content: start;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    height: 200px;
    
}
#certificate{
    width: 100%;
    height: 80%;
    border: 2px solid transparent;
}
#certificate:hover{
    transform: translate(-20%,-20%) scale(1.5);
    transition: transform ease-in-out 0.2s 0.2s;
    border-color: #f00;
}
#coreinterests{
    /* background-color: 	#ede5ff; */
    background: url(https://images.pexels.com/photos/1261728/pexels-photo-1261728.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    background-size: cover;
    opacity: 0.8;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
    width: 100%;
    height: 100%;
    text-align: center;
}

@media (max-width: 767px) {
    #certificate{
        width: 80%;
        height: 45%;
        border: 2px solid transparent;
    }
   
#coreinterestsp{
    text-align: center;
    flex: 1 1;
    line-height: 1.2;
    align-content:flex-start;
    display: flex;
    flex-wrap: wrap;
    font-size:small;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
}

#skillp{
    font-size:small;
}
h1{
    font-size: larger;
}
#skills{
    flex: 2;
}
}