/*Top Level CSS*/
#Introduction{
    display: flex;
    gap: 20px;
    padding: 30px;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    /* background: #F9FBE7; */
    background: white;
}
#p1{
    overflow-y: hidden;
    overflow-x: hidden;
}
/*Page Layout*/
#header1{
flex: 1;
display: flex;
flex-direction: row;
padding: 10px;
}
#body{
flex: 3;
}
@keyframes slide {
    from {
      transform: translateY(-25px);
    }
    to {
      transform: translateY(0px);
    }
  }
@keyframes rotatea {
    from {
      transform: rotateY(0deg);
    }
    to {
      transform: rotateY(1800deg);
    }
  }
#Title{
    border:0px;
    margin: 0px;
    flex:1;
    -webkit-text-stroke-width: 0.20px;
    font-size:x-large;
    color: #000000;
    font-family: "Impact", Arial, sans-serif;
    animation: slide 2s;
}
#Title:hover{
transform: rotateY(720deg);
transition: transform ease-in-out 1s 1s;
}
#subheading{
    color: #808080;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
#imagecontainer{
    flex:2;
    display:flex;
    justify-content: center;
    align-content: center;
}
#iconsContainer{
    flex:1;
    display: flex;
    justify-content: center;
   
}

/*Functional Animations*/
#cover{
    width: 176px;
    height: 160px;
    background-size: 60%;
    border-radius: 50%;
    box-shadow: 0px 0px 3px 3px #333333;
    animation: rotatea  ease-in-out 0.5s 0.5s;
}
#cover:hover{
    transform:  scale(1.2);
    transition: transform 0.5s ease-in;
}
#cover:not(:hover){
    transform:  scale(1.0);
    transition: transform 0.5s ease-in;
}

#CrossBlackBoard{
    position: absolute;
    bottom: 10%;
    left: 10%;
    right: 10%;
    top: 35%;
    background-color: #DABF9E;
    transform: rotateZ(-2.1deg);
    padding: 20px;
    display: flex;
    overflow-y: scroll;
    box-shadow: 20px 20px 5px lightgray;

}
#CrossBlackBoard::-webkit-scrollbar{
    display: none;
}
#BlackText{
   
    transform: rotateZ(2deg);
    position: absolute;
    top: 20%;
    left: 10%;
    right: 10%;
    padding: 1px;
}
span{
    /* background: url("https://www.solidbackgrounds.com/images/1024x1024/1024x1024-white-solid-color-background.jpg");
    background-clip: text; */
    color: #333333;
    font-size: larger;
    text-align: justify;
    font-family: 'Brush Script MT', cursive;
    -webkit-text-stroke: 1px;
    
}

@media (max-width: 767px) {
    #cover{
        width: 88px;
        height: 80px;
    }
    #Title{
    font-weight: bolder;
    font-size: 1.3rem;
    font-style: italic;
    font-family: Verdana, Arial, sans-serif;
    color: #444444;
    padding-right: 10px;
    }
    #subheading{
        color: #808080;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    }
    #imagecontainer{
        flex:2;
        display:flex;
        justify-content: space-between;
        align-content: center;
    }
  }