#PageContainer{
  margin: 0;
  height: 300%;
  width: 100%;
  padding: 0px;
  transform-style: preserve-3d;
  perspective: 2px;
}

#PageContainer>div
{
  overflow-x: hidden;
  width: 100%;
  height: 100vh;
  padding: 0px;
  margin: 0px;
  /* transform: translateZ(0); */
}

#PageContainer>div::-webkit-scrollbar {
  width: 4px; 
}
#PageContainer>div::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 4px; 
}

#PageContainer>div::-webkit-scrollbar-thumb:hover {
  background-color: lightgrey;
}

#PageContainer>div::-webkit-scrollbar-track {
  background-color: #ddd;
}
/* App.css */
.parallax {
  transition: transform 0.2s ease-out; /* Apply a smooth transition */
}

#p1 {
  transform: translateY(var(--fact1, 0px));
}

#p2 {
  transform: translateY(var(--fact2, 0px));
}

#p3 {
  transform: translateY(var(--fact3, 0px));
}
